.transactionhistory {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 450px;
  border: 1px solid #ddd;
  padding: 8px;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.tr:nth-child(even) {
  background-color: #f2f2f2;
}
.tr:hover {
  background-color: #ddd;
}

.th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04aa6d;
  color: white;
}
